@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;700;800&family=Poppins:wght@100;400&family=Source+Code+Pro:wght@200;300;400&family=Unica+One&display=swap');

:root {
    --headerFont: 'Unica One', cursive;
    --mainFont: 'Open Sans', sans-serif;
    --altText: 'Poppins', sans-serif;
}

*, ::before, ::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: var(--altText);
    background-color: lightblue;
    overflow-x: hidden;
}

a {
    text-decoration: none;
    color: rgb(255, 255, 255);
}
