.primary-header {
    backdrop-filter: blur(24px);
    background: hsl(181, 100%, 38%, 0.1);
    font-family: var(--headerFont);
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 8vh;
    width: 100%;
    padding: 0 3.125em;
    z-index: 5;
}

nav {
    position: fixed;
    top: 8vh;
    right: 0;
    height: 100vh;
    width: 50%;
    background-color: hsl(260, 6%, 19%);
    transition: 150ms ease-in-out;
}

.menu-inactive {
    transform: translateX(100%);

}

.nav-links {
    list-style-type: none;
}

.menu {
    display: none;
}

@media only screen and (min-width: 23.75em) {
    /* .primary-header {
        padding: 0 1.53em;
    }

   .nav-links {
    margin-top: 6em;
   }

   .nav-links a {
    padding: 2em .3em;
   } */

    .menu {
        display: block;
        color: white;
        cursor: pointer;
        z-index: 9999;
    }
}

@media only screen and (min-width: 30em) {
    .menu {
        display: block;
        color: white;
        cursor: pointer;
        z-index: 9999;
    }

    .nav-links {
        margin-top: 6em;
        list-style-type: none;
    }

    .nav-links a {
        display: block;
        padding: 0.75em 2em;
        margin: 8px;
        font-size: 1.25rem;
    }

    .nav-links a:hover {
        background-color: hsl(260, 6%, 10%);
        border-radius: 6px;
    }

}

@media only screen and (min-width: 768px) {
    .menu {
        display: none;
    }

    .menu-inactive {
        transform: translateX(0);
    }

    nav {
        background: none;
        position: unset;
        top: auto;
        right: auto;
        height: auto;
        width: auto;
    }

    .nav-links {
        display: flex;
        padding: 0;
        margin: 0;
    }

    .nav-links a {
        padding: 0;
        margin: 0 20px;
        background: none;
    }

    .nav-links a:hover {
        background: none;
    }

}