.hero-section {
    position: relative;
    min-height: 100vh;
    background-color: #202022;
    /* display: grid;
    place-items: center; */
}

.hero-container {
    /* height: 80vh; */
    margin: auto;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px;
}

.profile {
    width: 200px;
    height: 300px;
    margin-top: 2vh;
}

.avatar {
    background-image: url('../images/avatar1.jpg');
    background-size: cover;
    background-position: center;
    background-position-x: 23%;
    border-radius: 8px;
    height: inherit;
    width: inherit;
}

.introduction {
    padding: 30px;
    color: white;
}

.important-links {
    position: absolute;
    display: flex;
    flex-direction: column;
    left: 50px;
    top: 130px;
    gap: 10px;
}

.important-links i {
    font-size: 3rem;
}

.my-text {
    position: relative;
    width: max-content;
    color: rgb(255, 255, 255);
    font-size: 1.90rem;
    margin-left: 35px;
    filter: blur(.95px);
}

.my-text::before,
.my-text::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.my-text::before {
    background-color: #202022;
    animation: typewriter 2200ms steps(23) 1s forwards;
}

.my-text::after {
    width: 0.075em;
    background-color: antiquewhite;
    animation: 
    typewriter 2200ms steps(23) 1s forwards,
    blink 750ms steps(23) infinite;
}

@keyframes typewriter {
    to {left: 100%;}
}

@keyframes blink {
    to {
        background: transparent;
    }
}

.punctuation {
    color: gray;
}

.tag {
    color: #52a0ec;
}

.attr-name {
    color: #87cefa;
}

.attr-value {
    color: #faa570;
}

.contacts {
    position: absolute;
    bottom: 0;
    width: 100%;
    margin: 0 auto;
    color: white;
}

.contacts-wrapper {
    display: flex;
    justify-content: center;
    margin: 20px 0;
}

.contacts-wrapper p{
    font-family: var(--headerFont);
    font-weight: normal;
    font-size: .90rem;
    padding: 0 8px;
    color: white;
}

.contacts-wrapper i {
    display: flex;
}

.contact-icons {
    background-image: linear-gradient(45deg, rgb(90, 151, 216), rgb(69, 189, 185), rgba(54, 163, 246, 0.444), rgb(222, 145, 38));
    color: transparent;
    background-size: 300%;
    background-clip: text;
    animation: icons-animation 10s infinite alternate;
}

@keyframes icons-animation {
    0% {
        background-position: left;
    }

    100% {
        background-position: right;
    }

}

@media only screen and (max-width: 23.75em) {
    .hero-section {
        margin: 0 auto;
    }
    
    .hero-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .introduction {
        font-size: .70rem;
        margin-left: -20px;
        width: 100%;
        margin-right: 120px;
    }

    .my-text {
        font-size: 1.5rem;
    }

    .important-links {
        left: 20px;
        top: 120px;
    }

    .important-links i {
        color: rgb(167, 68, 68);
    }

    .contacts {
        padding: 0 25px;

    }

    .contacts-wrapper p {
        font-size: .75rem;
        margin-bottom: 30px;
    }
}

@media only screen and (min-width: 30em) {

    .important-links {
        left: 40px;
        top: 170px;
    }

    .profile {
        height: 320px;
        width: 250px;
        margin-top: 8vh;
    }
}


@media only screen and (min-width: 42.5em) {
    .important-links {
        left: 100px;
        top: 170px;
    }

    .profile {
        width: 250px;
        margin-top: 10vh;
    }
}


@media only screen and (min-width: 768px ) {
    .menu {
        display: none;
    }

    .contacts-wrapper {
        justify-content: space-around;
    }
}


@media only screen and (min-width: 1025px) {
    .hero-container {
        flex-direction: row;
    }

    .profile {
        margin-top: 10px;
        width: 250px;
        height: 400px; 
    }

    .avatar {
        background-position: center;
        background-position-x: 35%;
    }

    .hero-container {
        background-color: #202022;
        height: 75vh;
        border-radius: 10px;
    }
    
    .contacts-wrapper p {
        color: #202022;
        font-size: 1.25rem;
    }
    
    .hero-section {
        position: relative;
        background-color: rgb(131, 172, 206);
        display: grid;
        place-items: center;
    }

    .important-links {
        position: absolute;
        left: -60px;
    }
    
    .contact-icons {
        background-image: linear-gradient(45deg, rgb(216, 90, 111), rgb(25, 77, 75), rgba(3, 24, 40, 0.444), rgb(138, 81, 1));
        color: transparent;
        background-size: 300%;
        background-clip: text;
        animation: icons-animation 10s infinite alternate;
    } 
}

