@media only screen and (min-width: 1301px) {
    .primary-header {
        background-color: hsl(0, 0%, 50%, 0/1 );
        backdrop-filter: blur(16px);
    }

    .hero-section {
        justify-content: center;
        background-color: inherit;
        margin: 0 auto;
        max-width: 1200px;
        padding: 50px;
    }

    .hero-container {
        width: 100%;
        padding: 20px;
        flex-direction: row;
        justify-content: space-between;
    }

    .introduction {
        font-size: 1.25rem;
    }
    
    .profile {
        margin-top: 10px;
        width: 250px;
        height: 400px; 
    }

    .avatar {
        background-position: center;
        background-position-x: 35%;
    }

    .contact-icons {
        background-image: linear-gradient(45deg, rgb(216, 90, 111), rgb(25, 77, 75), rgba(3, 24, 40, 0.444), rgb(138, 81, 1));
        color: transparent;
        background-size: 300%;
        background-clip: text;
        animation: icons-animation 10s infinite alternate;
    }

    .contacts-wrapper p:hover {
        color: hsl(0, 0%, 45%)
    }
    
}
